.page-header {
  display: flex;
  flex-direction: column;
  background-color: #242424;
  color: #ffffff;
}

.header__logo {
  box-sizing: border-box;
  height: 85px;
  text-align: center;
  padding-top: 29px;
}

.header__logo-image {
  width: 226px;
  height: 30px;
}

.header-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.site__item-link {
  position: relative;
  display: block;
  padding: 12px 21px;
  padding-bottom: 13px;
  color: #ffffff;
  font-family: "PT Sans Narrow", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #826550;
  border-top: 1px solid #886e5b;
  border-bottom: 1px solid #71543f;
}

.header-nav {
  position: relative;
  background-color: #826550;
  min-height: 51px;
}

.header-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  display: block;
  box-sizing: border-box;
  width: 70px;
  height: 51px;
  border: 0;
  background-color: #6A4D38;
  cursor: pointer;
}

.header-nav--closed .header-nav__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff;
}

.header-nav--opened .header-nav__toggle {
  top: 0;
  right: auto;
  left: 0;
}
.header-nav--opened .header-nav__toggle::before, .header-nav--opened .header-nav__toggle::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
}
.header-nav--opened .header-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.header-nav--opened .header-nav__toggle::after {
  transform: rotate(-45deg);
}

.user-list__login {
  position: relative;
  display: block;
  padding: 13px 21px;
  padding-bottom: 14px;
  padding-left: 47px;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none;
  font-family: PT Sans Narrow;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
}

.user-list__login-icon {
  position: absolute;
  top: 17px;
  left: 21px;
  width: 16px;
  height: 16px;
  fill: #ffffff;
}

@media (max-width: 767px) {
  .header-nav--closed .site__item {
    display: none;
  }
  .header-nav--closed .site__item--active {
    display: block;
  }

  .header-nav--closed .user-list__login {
    display: none;
  }

  .header-nav--closed .site__item--active {
    padding-right: 70px;
    padding-left: 70px;
    text-align: center;
  }

  .header-nav--opened .header-nav__wrapper {
    position: absolute;
    left: 70px;
    top: 0;
    right: 0;
    min-height: 80vh;
    z-index: 3;
    background-color: #6a4d38;
  }

  .header-nav--opened .header-nav__wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 7px;
    height: 100%;
    box-shadow: inset 4px 0 7px -2px rgba(0, 0, 0, 0.6);
  }

  .header-nav--nojs .header-nav__toggle {
    display: none;
  }

  .header-nav--nojs .header-nav__wrapper {
    position: static;
    min-height: 0;
  }
}
